import "../stylus/main.styl";

const header = document.querySelector("header");
const nav = document.querySelector("nav");
const gigsContainer = document.querySelector(".gigs-container");
const gigs = gigsContainer ? gigsContainer.querySelector(".gigs") : null;
const sidebar = document.querySelector(".sidebar-container");
const desktop = window.matchMedia('(min-width: 700px)');
const mobile = window.matchMedia('(max-width: 700px)');
const handleDesktop = (e) => {
  if(e.matches) {
    sidebar.classList.remove("hide");
    sidebar.appendChild(nav);
    if(gigs) {
      sidebar.appendChild(gigs);
    }
  }
};
const handleMobile = (e) => {
  if(e.matches) {
    sidebar.classList.add("hide");
    header.appendChild(nav);
    if(gigs) {
      gigsContainer.appendChild(gigs);
    }
  }
};

desktop.addListener(handleDesktop) 
mobile.addListener(handleMobile); 

handleDesktop(desktop);
handleMobile(mobile);
